/* --------------------------------------------- NAVBAR -------------------------------------- */
.navbar {
  background-color: var(--navbar-color);
  color: var(--dark-font-color);
  width: 100vw;
  height: 45px;
  border-bottom: 1px solid var(--light-grey-color);
  position: fixed;
  top: 0;
  font-family: 'Comfortaa', cursive;
  padding: 0 2.5em;
  z-index: 103;
}

.navbar .left {
  font-size: 26px;
  color: var(--logo-color);
  font-weight: 700;
}

.navbar .right {
  /* width: 45vw; */
  font-size: 0.9em;
}

.navbar .right .element {
   display: inline-block;
  position: relative;
  color: var(--light-dark-font-color);
  display: flex;
  align-items: center;
  justify-content: center;
}

.navbar .right .element:after {
  content: '';
  position: absolute;
  width: 100%;
  transform: scaleX(0);
  height: 2px;
  bottom: 0;
  left: 0;
  background-color: var(--light-logo-color);
  transform-origin: bottom right;
  transition: transform 0.25s ease-out;
}

.navbar .right .element:hover:after {
  transform: scaleX(1);
  transform-origin: bottom left;
}


.navbar .right .resume {
  font-size: 14px;
  color: var(--logo-color);
  font-weight: 700;
  border: 1px solid var(--logo-color);
  padding: 0.2em 0.8em;
  border-radius: 20px;
  margin-left: 2em;
  cursor: pointer;
  width: 90px;
  height: 30px;
}

.navbar .right .resume:hover {
  box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
}

.navbar .resume .text {
  animation: resumeHover 0.3s 1 linear;
}

.navbar .resume .icon {
  animation: resumeHover 0.3s 1 linear;
}

@keyframes resumeHover {
  0% {
    transform:scale(0.3); 
  }
  50% {
    transform:scale(1); 
  }
  75% {
    transform:scale(1.3); 
  }
  100% {
    transform:scale(1); 
  }
}

.navbar .right .sun {
  animation: sunEntry 0.3s 1 linear;
}

.navbar .right .moon {
  animation: moonEntry 0.3s 1 linear;
}

.navbar .right .element-letter--admin {
  color: var(--lighter-dark-font-color);
  margin: 0 0.4em 0.1em 0.6em;
  font-family: 'Comfortaa', cursive;
  font-size: 1.7em;
  font-weight: bold;
}

.navbar .right .element-letter--admin:hover {
  color: var(--light-logo-color);
}

.navbar .right .element--admin {
  color: var(--lighter-dark-font-color);
  margin: 0 0.6em;
}

.navbar .right .element--admin:hover {
  color: var(--light-logo-color);
}

.navbar .right .sound-on {
  color: var(--light-logo-color);
  margin-right: 1.9em;
}

.navbar .right .sound-off {
  color: var(--light-icon-color);
  margin-right: 1.9em;
}

@keyframes sunEntry {
  from {
    opacity: 0;
    transform: rotate(270deg);
  }
  to {
    opacity: 1;
    transform: rotate(360deg);
  }
}

@keyframes moonEntry {
  from {
    opacity: 0;
    transform: rotate(90deg);
  }
  to {
    opacity: 1;
    transform: rotate(0deg);
  }
}


@media only screen and (max-width: 786px) {
  .navbar {
  padding: 0 1em;
}
  .navbar .right {
    display: none;
  }

  .navbar .right-mini {
    width: auto;
  }

  .navbar .right-mini .sun {
  animation: sunEntry 0.3s 1 linear;
  margin: 0 25px 0 0;
}

.navbar .right-mini .moon {
  animation: moonEntry 0.3s 1 linear;
  margin: 0 25px 0 0;
}

  .navbar .right-mini .music-on {
  margin: 0 25px;
}

.navbar .right-mini .music-off {
  margin: 0 25px;
}

.navbar .right-mini .sound-on {
  color: var(--light-logo-color);
}

.navbar .right-mini .sound-off {
  color: var(--light-icon-color);
}

    .navbar .mobile_logo {
        cursor: pointer;
        animation: verticalToHorizontal 0.3s 1 linear;
        color: var(--light-dark-font-color);
        padding-top: 0.1em;
    }

   .navbar .mobile_logo--tilted {
      cursor: pointer;
      transform: rotate(90deg);
      animation: horizontalToVertical 0.3s 1 linear;
      color: var(--light-dark-font-color);
  }

  .navbar .mobile_logo:hover, 
  .navbar .mobile_logo--tilted:hover {
    color: var(--logo-color);
  }

  @keyframes verticalToHorizontal {
        0% {
            transform: rotate(90deg);
        }
        50% {
            transform: rotate(45deg);
        }
        100% {
            transform: rotate(0deg);
        }
    }

    @keyframes horizontalToVertical {
        0% {
            transform: rotate(0deg);
        }
        50% {
            transform: rotate(45deg);
        }
        100% {
            transform: rotate(90deg);
        }
    }

}

