  0% {
    width: 50px;
  }
  25% {
    width: 75px;
  }
  50% {
    width: 110px;
  }
  75% {
    width: 145px;
  }
  100% {
    width: 180px;
  }
}

@keyframes closeSmoothlySidebar {
  0% {
    width: 180px;
  }
  25% {
    width: 145px;
  }
  50% {
    width: 110px;
  }
  75% {
    width: 75px;
  }
  100% {
    width: 50px;
  }
}



