/* ----------------- SPinner ------------------------- */
.spinner-new {
  display: flex;
  width: 3.5em;
  height: 3.5em;
  border: 3px solid transparent;
  border-top-color: var(--logo-color);
  border-bottom-color: var(--logo-color);
  border-radius: 50%;
  animation: spin 1.5s linear infinite;
}

.spinner-new:before {
  content: '';
  display: block;
  margin: auto;
  width: 0.75em;
  height: 0.75em;
  border: 3px solid var(--logo-color);
  border-radius: 50%;
  animation: pulse 1s alternate ease-in-out infinite;
}

.spinner-new-block {
  display: flex;
  width: 2em;
  height: 2em;
  border: 3px solid transparent;
  border-top-color: var(--logo-color);
  border-bottom-color: var(--logo-color);
  border-radius: 50%;
  animation: spin 1.5s linear infinite;
}

.spinner-new-block:before {
  content: '';
  display: block;
  margin: auto;
  width: 0.75em;
  height: 0.75em;
  border: 3px solid var(--logo-color);
  border-radius: 50%;
  animation: pulse 1s alternate ease-in-out infinite;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

@keyframes pulse {
  from {
    transform: scale(0.5);
  }
  to {
    transform: scale(1);
  }
}


