.delete-card .title {
  font-size: 1.2em;
  color: var(--bodinga-color);
}

.delete-card--dark {
  border: 1px solid var(--light-dark-font-color);
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
}

.delete-card .button {
  background-color: transparent;
  box-shadow: none;

}

.delete-card .button-yes{
display:inline-block;
padding:0.2em 1em;
border:0.1em solid var(--gotuu-color);
box-sizing: border-box;
text-decoration:none;
font-family:'Roboto',sans-serif;
font-weight:300;
color: var(--gotuu-color);
text-align:center;
transition: all 0.2s;
border-radius: 5px;
cursor: pointer;
background-color:var(--card-color);
}

.delete-card .button-yes:hover{
background-color:var(--light-gotuu-color);
}

.delete-card .button-no{
display:inline-block;
padding:0.2em 1em;
border:0.1em solid var(--bodinga-color);
box-sizing: border-box;
text-decoration:none;
font-family:'Roboto',sans-serif;
font-weight:300;
color:var(--bodinga-color);
text-align:center;
transition: all 0.2s;
border-radius: 5px;
cursor: pointer;
background-color:var(--card-color);
}

.delete-card .button-no:hover{
background-color:var(--light-bodinga-color);
}

/* ------------------------ COMMENT CARD -------------------- */
.comment-card {
  color: var(--lighter-dark-font-color);
  background-color: var(--card-color);
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  border-radius: 10px;
  z-index: 1;
  font-family: 'Comfortaa', cursive;
  padding: 10px;
  width: 400px;
  min-height: 130px;
  font-size: 0.8em;
}

.comment-card .title {
  font-size: 1.2em;
  color: var(--logo-color);
}

.comment-card--dark {
  border: 1px solid var(--light-dark-font-color);
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
}

.comment-card .text {
  margin-top: 2em;
}

@media only screen and (max-width: 550px) {
  .comment-card {
  width: 270px;
}
}
